@-webkit-keyframes rollOut {
  0% {opacity: 1; -webkit-transform: translateX(0px) rotate(0deg);}
  100% {opacity: 0; -webkit-transform: translateX(100%) rotate(120deg);}
}

@-moz-keyframes rollOut {
  0% {opacity: 1; -moz-transform: translateX(0px) rotate(0deg);}
  100% {opacity: 0; -moz-transform: translateX(100%) rotate(120deg);}
}

@-ms-keyframes rollOut {
  0% {opacity: 1; -ms-transform: translateX(0px) rotate(0deg);}
  100% {opacity: 0; -ms-transform: translateX(100%) rotate(120deg);}
}

@-o-keyframes rollOut {
  0% {opacity: 1; -o-transform: translateX(0px) rotate(0deg);}
  100% {opacity: 0; -o-transform: translateX(100%) rotate(120deg);}
}

@keyframes rollOut {
  0% {opacity: 1; transform: translateX(0px) rotate(0deg);}
  100% {opacity: 0; transform: translateX(100%) rotate(120deg);}
}

@mixin rollOut($duration: $durationDefault, $delay: $delayDefault, $function: $functionDefault, $fill: $fillDefault, $visibility: $visibilityDefault) {
  @include animation-name(rollOut);
  @include duration($duration);
  @include delay($delay);
  @include function($function);
  @include fill-mode($fill);
  @include visibility($visibility);
}
